.wrapper {
  width: 100%;
  height: 100%;
  background-color: white;
  padding-top: 40px; }

.login {
  margin: auto;

  border: 1px solid #1f8bff;
  background-color: white; }

.login__h2 {
  margin-bottom: 20px; }

.submitFloor {
  display: flex;
  align-items: center; }




@media all and (max-width: 600px) {
  .login {
    width: 80%;
    padding: 20px 20px; }

  .submitFloor__btn {
    width: 100%;
    margin: auto; }

  .login__h2 {
    text-align: center; }

  .login__label {
    text-align: center; } }

@media all and (min-width: 600px) {
  .login {
    width: 30%;
    padding: 30px 40px; }
  .submitFloor__btn {
    margin-right: 20px; } }
